.popup-preview {
  .mfp-close {
    right: 100px;
    font-size: 0;
    &::after {
      display: inline-block;
      position: relative;
      top: 28px;
      right: 0;
      height: 20px;
      width: 20px;
      content: "";
      background: url("../images/icons/mfp-close.svg") no-repeat right;
    }
  }
}
.popup-preview-slider {
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  padding: 48px 64px;

  @media (max-width: 1024px) {
    min-height: 100vh;
    flex-direction: column-reverse;
    align-items: center;
  }

  @media (max-width: 768px) {
    padding: 0;
  }

  @media (max-width: 650px) {
    min-height: 100vh;
    justify-content: center;
  }

  .popup-preview-slider-vertic {
    width: 19%;
    height: 100%;

    @media (max-width: 1024px) {
      width: 80%;
    }

    @media (max-width: 650px) {
      display: none;
    }
  }

  .popup-preview-slider-main {
    width: 75%;
    min-height: 100%;
    padding-top: 25px;

    @media (max-width: 1024px) {
      width: 80%;
    }
  }

  .swiper {
    margin: 0;

    &.vertical-slider {
      height: 700px;

      .swiper-wrapper {
        justify-content: center;
      }

      @media (max-width: 992px) {
        height: 280px;
      }

      .swiper-slide {
        img {
          border: 2px solid #fff;
        }

        &-active {
          img {
            border: 2px solid #ffc100;
          }
        }
      }
    }
  }
}

.swiper-pagination {
  display: none;

  @media (max-width: 650px) {
    display: block;
  }
}

.swiper-pagination-bullet {
  width: var(
    --swiper-pagination-bullet-width,
    var(--swiper-pagination-bullet-size, 24px)
  );
  height: var(
    --swiper-pagination-bullet-height,
    var(--swiper-pagination-bullet-size, 2px)
  );
  border-radius: 2px;
}

.swiper-pagination-bullet-active {
  background-color: #2a4455;
}

.slider-previes-container {
  position: relative;
  padding: 45px 0px;

  @media (max-width: 650px) {
    display: none;
  }
}

.preview-btn-next {
  color: #071c28;
  position: absolute;
  transform: rotate(270deg);
  top: 10px;
  right: 0;
  left: 45px;
  &:after {
    font-size: rem(20);
  }

  @media (max-width: 1024px) {
    transform: rotate(180deg);
    top: 100px;
    right: 0;
    left: -45px;
  }
}
.preview-btn-prev {
  color: #071c28;
  position: absolute;
  transform: rotate(270deg);
  // bottom: 10px;
  top: 95%;
  right: 0;
  left: 45px;
  &:after {
    font-size: rem(20);
  }

  @media (max-width: 1024px) {
    transform: rotate(180deg);
    top: 28%;
    right: 0px;
    left: 100%;
  }
}
.main-silder-container {
  position: relative;
  padding: 0px 30px;
  text-align: center;

  @media (max-width: 650px) {
    padding: 0;
  }
}

.main-button-next {
  display: inline-block;
  position: absolute;
  right: 0;
  top: 50%;
  width: 25px;
  height: 40px;
  background-image: url(.././images/icons/arrow-slider-preview-next.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;

  &.swiper-button-disabled {
    opacity: 0.35;
  }

  @media (max-width: 650px) {
    display: none;
  }
}
.main-button-prev {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 50%;
  width: 25px;
  height: 40px;
  background-image: url(.././images/icons/arrow-slider-preview-next.svg);
  transform: rotate(180deg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;

  &.swiper-button-disabled {
    opacity: 0.35;
  }

  @media (max-width: 650px) {
    display: none;
  }
}

.swiper-vertical > .swiper-wrapper {
  justify-content: center;
}
