.main-contacts-tel-arrow {
  display: inline-block;
  position: absolute;
  margin-left: 6px;
  bottom: 2px;
  max-width: 12px;
  cursor: pointer;
}
.main-contacts-tel-arrow img {
  min-width: 12px;
}

.main-contacts-tel-link:hover {
  color: #2A4455;
}
.main-contacts-tel-numbers {
  display: none;
  position: absolute;
  z-index: 15;
  max-width: 222px;
  left: 0;
  top: 55px;
  background: #ffffff;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.1);
  border-radius: 0px 0px 8px 8px;
  padding: 15px 16px;
  border-top: 4px solid #bc0000;
}
.main-contacts-tel-wrap {
  margin-bottom: 18px;
  padding-bottom: 19px;
  border-bottom: 1px solid #d3d3d3;
  padding-left: 8px;
}
.main-contacts-tel-wrap .footer-bottom-socials-link {
  border: 1px solid #d3d3d3;
  margin-right: 0;
}
.main-contacts-tel-wrap .socials-link-instagram:hover {
  background: #8CB2D6;
  border: 1px solid transparent;
}
.main-contacts-tel-wrap .socials-link-instagram:hover .footer-bottom-socials-svg {
  fill: #1A66AE;
}
.main-contacts-tel-wrap .socials-link-linked:hover {
  background: #8CB2D6;
  border: 1px solid transparent;
}
.main-contacts-tel-wrap .socials-link-linked:hover .footer-bottom-socials-svg {
  fill: #1A66AE;
}
.main-contacts-tel-wrap .socials-link-youtube:hover {
  background: #DF908A;
  border: 1px solid transparent;
}
.main-contacts-tel-wrap .socials-link-youtube:hover .footer-bottom-socials-svg {
  fill: #BC0000;
}
.main-contacts-tel-wrap .socials-link-facebook:hover {
  background: #8CB2D6;
  border: 1px solid transparent;
}
.main-contacts-tel-wrap .socials-link-facebook:hover .footer-bottom-socials-svg {
  fill: #1A66AE;
}
.main-contacts-tel-wrap .footer-bottom-socials-svg {
  fill: #d3d3d3;
}
.main-contacts-tel-wrapp {
  gap: 8px;
  border: none;
  margin-bottom: 0;
  padding-bottom: 0;
  padding-left: 0;
}
.main-contacts-tel-title {
  font-size: 0.75rem;
  color: rgba(0, 0, 0, 0.7);
  margin-bottom: 6px;
}
.main-contacts-tel-num {
  display: block;
  font-weight: 700;
  font-size: 0.875rem;
  margin-bottom: 11px;
  padding-left: 24px;
  position: relative;
}
.main-contacts-tel-num:hover {
  color: #2A4455;
}
.main-contacts-tel-num-img {
  position: absolute;
  transform: translate(0, -50%);
  top: 50%;
  left: 0;
  max-width: 16px;
}
.main-contacts-tel-num-img img {
  display: inherit;
}
.main-contacts-tel-mail {
  color: #1a66ae;
  font-weight: 400;
  font-size: 1rem;
  margin-bottom: 0;
}
.main-contacts-tel-viber {
  font-weight: 400;
  font-size: 0.875rem;
  margin-bottom: 5px;
}
.main-contacts-tel-tele {
  font-weight: 400;
  font-size: 0.875rem;
  margin-bottom: 0;
}

.numbers-open {
  display: block;
}

