.popup-preview {
  position: relative;
  max-width: 1400px;
  margin: 0px auto;
  padding-left: 40px;
  padding-right: 40px;
  .mfp-close-btn-in .mfp-close {
    font-size: 35px;
    color: #848484;
    right: 112px;
    top: 32px;
  }
}

.popup-form-reviews {
  position: relative;
  margin: 20px auto;
  max-width: 696px;
  @media (max-width: 576px) {
    margin: 0;
  }
  // .mfp-close {
  //   font-size: 50px;
  //   top: 26px;
  //   right: 26px;
  //   @media (max-width: 780px) {
  //     // position: fixed;
  //   }
  // }

  .row {
    justify-content: center;
  }

  .popup-agreement-txt {
    @media (max-width: 1024px) {
      font-size: 14px;
    }

    &::before {
      content: "Нажимая кнопку «Отправить отзыв» вы соглашаетесь";
    }
    @media (max-width: 1024px) {
      &::before {
        content: "Я соглашаюсь с";
      }
    }
  }

  .form-reviews {
    @media (max-width: 780px) {
      // position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #fff;
      bottom: 0;
      // position: fixed;
      overflow-y: scroll;
      overflow-x: hidden;
    }

    &-content {
      background: #fff;
      max-width: 696px;
      height: 100%;
      margin: 0px auto;
      border-radius: 10px;

      @media (max-width: 680px) {
        width: 100%;
      }
    }

    &-header {
      padding: 34px 48px;
      border-bottom: 1px solid #d3d3d3;

      @media (max-width: 680px) {
        padding: 15px;
      }

      &-title {
        font-weight: 700;
        font-size: 28px;
        color: #000;
        margin-bottom: 15px;
      }

      &-item {
        font-weight: 400;
        font-size: 16px;
        margin-bottom: 12px;

        span:nth-child(1) {
          @media (max-width: 680px) {
            display: none;
          }
        }

        span:nth-child(2) {
          font-weight: 700;
          font-size: 16px;
          color: #1a66ae;
        }
      }
    }

    &-body {
      padding: 34px 48px;

      @media (max-width: 680px) {
        padding: 24px 15px;
      }

      .form-cell {
        display: flex;
        justify-content: space-between;
        margin-bottom: 17px;

        @media (max-width: 680px) {
          flex-direction: column;
          margin-bottom: 0;
        }

        &.load {
          margin-bottom: 48px;
        }

        &.load-file {
          margin-bottom: 48px;
        }

        label {
          font-weight: 400;
          font-size: 14px;
          color: #000;

          span {
            color: #bc0000;
          }
        }

        .url {
          margin-top: 0;
          transition: all 0.2s ease-out;
          cursor: pointer;

          &:hover {
            border: 1px solid #fff;
            box-shadow: 0px 0px 8px rgb(0 0 0 / 10%);
          }
        }

        .customCheckbox {
          position: absolute;
          z-index: -1;
          opacity: 0;
        }

        .customCheckbox + label {
          display: inline-flex;
          align-items: center;
          user-select: none;
          margin-right: 8px;
          cursor: pointer;

          &:hover {
           &::before {
             border: 1px solid #808080;
           }
          }
        }

        .customCheckbox + label::before {
          content: "";
          display: inline-block;
          width: 1em;
          height: 1em;
          flex-shrink: 0;
          flex-grow: 0;
          border: 1px solid #D3D3D3;
          border-radius: 0.25em;
          margin-right: 0.5em;
          background-repeat: no-repeat;
          background-position: center center;
          background-size: 50% 50%;
        }

        .customCheckbox:checked + label::before {
          border-color: #071c28;
          background-color: #071c28;
          background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
        }

        input {
          width: 288px;
          height: 40px;
          border: 1px solid #d3d3d3;
          padding: 10px 16px;
          border-radius: 5px;
          margin-top: 5px;
          font-family: "Open Sans", sans-serif;

          @media (max-width: 680px) {
            width: 100%;
            margin-bottom: 25px;
          }

          &:focus {
            border: 1px solid #808080;
          }

          &:hover {
            border: 1px solid #808080;
          }
        }

        .card-item-rating-item {
          margin-bottom: 14px;
        }

        .btn-submit {
          padding: 9px 30px;
          background-color: #2a4455;
          color: #fff;
          font-weight: 700;
          border: none;
          font-size: 16px;
          border-radius: 5px;
          margin-top: 0;

          @media (max-width: 680px) {
            width: 100%;
          }
        }

        &-info {
          width: 100%;
          @media (max-width: 680px) {
            margin-bottom: 24px;

            input {
              margin-bottom: 0;
            }
          }
          span.error {
            color: red;
            font-size: 0.75rem;
            margin-bottom: 2px;
          }
        }

        &-field {
          &-textarea {
            position: relative;

            @media (max-width: 680px) {
              margin-bottom: 25px;
            }

            textarea {
              padding: 10px 16px;
              width: 100%;
              height: 104px;
              border: 1px solid #d3d3d3;
              border-radius: 5px;
              resize: none;
              margin-top: 5px;
              font-family: $ffOpenSans;
              &:focus {
                border: 1px solid #808080;
              }

              &::-webkit-scrollbar {
                background-color: transparent;
                width: 5px;
              }
              &::-webkit-scrollbar-thumb {
                background-color: #dfdfdf;
                border-radius: 10px;
              }
            }
          }

          &-quality {
            display: flex;
            justify-content: space-between;

            @media (max-width: 680px) {
              flex-direction: column;
              input {
                margin-bottom: 19px;

                &:last-child {
                  margin-bottom: 0;
                }
              }
            }

            .plus {
              border: 1px solid #559e53;
            }

            .minus {
              border: 1px solid #bc0000;
            }
          }

          &-load {
            input[type="file"] {
              display: none;
            }

            .error-file {
              color: #bc0000;
              font-size: 14px;
            }

            .img-file {
              width: 88px;
              height: 64px;
            }

            .load-file {
              border: 1px solid #ccc;
              display: inline-block;
              cursor: pointer;
              border-radius: 5px;
              height: 60px;
              padding: 15px 16px;
              width: 288px;
              transition: all 0.2s ease-out;

              &:hover {
                border: 1px solid #fff;
                box-shadow: 0px 0px 8px rgb(0 0 0 / 10%);
                span {
                  color: #2A4455;
                }

                svg {
                  fill: #2A4455;
                }
              }

              svg {
                width: 32px;
                height: 34px;
                fill: #2A4455;
              }

              @media (max-width: 680px) {
                width: 100%;
                margin-bottom: 25px;
              }

              span {
                position: relative;
                top: -12px;
                color: #000;
                padding-left: 12px;
                font-size: 16px;
              }
            }
          }

          &-url {
            position: relative;

            svg {
              width: 26px;
              height: 26px;
              position: absolute;
              z-index: 10;
              top: 18px;
              left: 18px;
              // padding: 18px;
            }

            span {
              position: initial;
              display: block;
              font-weight: 400;
              font-size: 14px;
              color: #BC0000;
            }

            input {
              height: 60px;
              position: relative;
              z-index: 9;
              padding-left: 66px;

              &::placeholder {
                color: #bc0000;
                font-size: 16px;
              }

              &:focus::-webkit-input-placeholder {
                color: transparent;
              }
            }
          }

          &-send {
            position: relative;
            display: flex;
            flex-direction: row;
            margin-right: 40px;

            @media (max-width: 1024px) {
              margin-bottom: 24px;
            }

            .popup-agreement-txt {
              flex: 0 0 352px;
              max-width: 352px;
              color: rgba(0, 0, 0, 0.5);
              padding-right: 33px;
            }

            .checkbox {
              margin-bottom: 0;
              top: 5px;
            }
          }
        }

        &-rating {
          display: inline-block;

          .rating-label {
            display: block;
          }

          .rating-set {
            position: relative;
            align-items: center;

            .card-item-rating-body {
              font-size: rem(50);

              &::before {
                color: #f9de8e;
              }
            }

            .error {
              position: absolute;
              bottom: -15px;
              left: 0;
              color: red;
              font-size: 0.75rem;
            }

            .card-item-rating-value {
              font-size: 28px;
            }

            // span.error {
            //   color: red;
            //   font-size: 0.75rem;
            // }
          }
        }
      }
    }

    // &-valid {
    //   span.error {
    //     position: absolute;
    //     bottom: -15px;
    //     left: 0;
    //     font-size: 11px;
    //     color: red;
    //   }
    // }
  }
}

.all-files {
  display: flex;
}

.all-files-item {
  position: relative;
  margin-right: 12px;
}

.remove-file {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 0;
  right: 0;
  font-weight: 600;
  color: #fff;
  background-color: rgba(255, 255, 255, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 999;

  &:hover {
    color: #ffc100;
  }
}

.type-file {
  padding: 5px;
  position: absolute;
  bottom: 6px;
  left: 0;
  color: #fff;
  background-color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  z-index: 999;
  font-size: 12px;
}

.open-files {
  width: 88px;
  height: 64px;
  text-align: center;
  padding: 18px 0;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
  transition: all 0.2s ease-out;

  &:hover {
    border: 1px solid #fff;
    box-shadow: 0px 0px 8px rgb(0 0 0 / 10%);
  }

  img {
    width: 26px;
    height: auto;
  }
}

.popup-close {
  // position: absolute;
  // top: 26px;
  // right: 26px;
  // line-height: 0.5;
  // font-size: 50px;
  // cursor: pointer;
  font-size: 0;
      opacity: 0.50;

  &::after {
    display: inline-block;
    position: absolute;
     top: 26px;
      right: 26px;
    height: 20px;
    width: 20px;
    content: "";
    background: url(../images/icons/mfp-close.svg) no-repeat right;

  }

  &:hover {
    opacity: 1;
  }
}

.popup-second {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 512px;
  height: auto;
  background: #fff;
  border-radius: 10px;
  padding: 34px 48px;

  @media (max-width: 1024px) {
    display: flex;
    justify-content: center;
    position: fixed;
    left: 50%;
    top: 50%;
    width: 100%;
    height: 100%;
  }
}

.popup-second-block {
  width: 100%;
}

.popup-second-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(29, 38, 44, 0.35);
  z-index: 9999;
}

.popup-second-title {
  margin: 0 0 15px 0;
}

.popup-second-text {
  margin-bottom: 20px;
}

.popup-second {
  .ui-btn {
    width: 48%;
  }

  @media (max-width: 576px) {
    padding: 34px 15px;
    align-items: center;
  }
}

.popup-second-btn-exit {
  background-color: #2a4455;
  color: #fff;
  margin-right: 15px;

  @media (max-width: 576px) {
    margin-bottom: 8px;

    &.ui-btn {
      width: 100%;
    }
  }
}

.popup-second-btn-cancel {
  @media (max-width: 576px) {
    &.ui-btn {
      width: 100%;
    }
  }
}

.popup-second-btn-cancel {
  background-color: #f5f5f5;
  color: #000;
}

#customCheckbox-error {
  position: absolute;
  font-size: 12px;
  top: 35px;
  left: 24px;
  width: 150px;
  color: red;

  @media (max-width: 327px) {
    top: 50px;
  }
}


.form-reviews-valid.popup-fastOder span.error {
  top: 50%;
  @media(max-width:680px) {
    transform: translate(0, -43%);
  }
}

.form-reviews-valid.popup-fastOder .popup-icon {
  top: 51%;
  @media(max-width:680px) {
    transform: translate(0, -43%);
  }
}

.form-reviews-valid.popup-fastOder .form-cell-info span.error {
  position: relative;
  left: 0;
}

.popup-form-reviews .form-reviews-body .form-cell input.valid {
  border-color: #559E53;
}

input[type="submit"] {
  // display: none;
  max-width: 200px;
  flex: 0 0 200px;
  height: 100px;
}

.popup-form-reviews .form-reviews-body .form-cell .url.error ~ .url-img {
  background-color: black;
}

.popup-form-reviews .form-reviews-body .form-cell .btn-submit {
  max-width: 200px;
  flex: 0 0 200px;
  height: 48px;
}